// import {Link} from "gatsby"
import React, { useState, useEffect } from 'react';

const Navbar = () => {
    
    const [scrolled, setScrolled] = useState(false);

    // change state on scroll
    useEffect(() => {
      const handleScroll = () => {
        const isScrolled = window.scrollY > 130;
        if (isScrolled !== scrolled) {
          setScrolled(!scrolled);
        }
      };
  
      document.addEventListener('scroll', handleScroll, { passive: true });
  
      return () => {
        // clean up the event handler when the component unmounts
        document.removeEventListener('scroll', handleScroll);
      };
    }, [scrolled]);
  
    return (
    
    <header className={scrolled ? "fixed h-24 sm:h-32 flex items-center z-30 shadow-sm border-b-2 border-pink-500 w-full bg-white dark:bg-gray-800" : "h-24 sm:h-32 flex items-center z-30 w-full dark:bg-gray-800"}>
        
    <div className= "container mx-auto px-6 flex items-center justify-between">
        <div className="uppercase text-gray-800 dark:text-white font-black text-3xl">
            <a href="#home"><span className="text-pink-500">Soham</span>.Manoli</a>
        </div>
        <div className="flex items-center">
            <nav className="font-sen text-gray-800 dark:text-white uppercase text-lg lg:flex items-center hidden">

            
                
                <a href="#about" className="py-2 px-6 flex">
                {/* <Link activeClass="active" to="about" spy={true} smooth={true} offset={-70} duration={500}/> */}
                About Me &nbsp; <span aria-label="jsx-a11y/accessible-emoji" role="img"></span>
                </a>
                {/* <a href="#about" className="py-2 px-6 flex">
                   Awards &nbsp; <span aria-label="jsx-a11y/accessible-emoji" role="img"></span>
                </a> */}
                <a href="#projects" className="py-2 px-6 flex">
                   Projects &nbsp; <span aria-label="jsx-a11y/accessible-emoji" role="img"></span>
                </a>
                <a href="#skills" className="py-2 px-6 flex">
                   Skills &nbsp; <span aria-label="jsx-a11y/accessible-emoji" role="img"></span>
                </a>
                <a href="#contact" className="py-2 px-6 flex">
                    Contact Me &nbsp; <span aria-label="jsx-a11y/accessible-emoji" role="img"></span>
                </a>
            </nav>
            <button className="lg:hidden flex flex-col ml-4">
                <span className="w-6 h-1 bg-gray-800 dark:bg-white mb-1">
                </span>
                <span className="w-6 h-1 bg-gray-800 dark:bg-white mb-1">
                </span>
                <span className="w-6 h-1 bg-gray-800 dark:bg-white mb-1">
                </span>
            </button>
        </div>
    </div>
</header>

    )
}

export default Navbar