import React from "react"
import About from "../components/about"
import Contact from "../components/contact"
import Home from "../components/home"
import Navbar from "../components/navbar"
import Skills from "../components/skills"
import Projects from "../components/projects"
import { Helmet } from "react-helmet"


const IndexPage = () => {
  return (
    <div>

        <Helmet>
          <meta charSet="utf-8" />
          <title>Soham.Manoli</title>
          {/* <link rel="canonical" href="msoham123.github.io" /> */}
        </Helmet>

      <Navbar></Navbar>
      <Home></Home>
      <About></About>
      <Projects></Projects>
      <Skills></Skills>
      <Contact></Contact>

     
      
<footer className="flex justify-center px-4 text-black bg-white dark:text-white dark:bg-gray-800">
        <div className="container py-6">

            <hr className="h-px mt-6 bg-gray-700 border-none"/>

            <div className="flex flex-col items-center justify-between mt-6 md:flex-row">
                <div>
                    <h1 className="text-xl font-bold"><span className="text-pink-500">SOHAM</span>.MANOLI</h1>
                </div>
                <div className="flex mt-4 md:m-0">
                    <div className="-mx-4">
                      <a href="#about" className="px-4 text-sm">About Me</a>
                      <a href="#projects" className="px-4 text-sm">Projects</a>
                      <a href="#skills" className="px-4 text-sm">Skills</a>
                      <a href="#contact" className="px-4 text-sm">Contact</a>
                      <a href="#home" className="px-4 text-sm">Back to Top</a>

                  	</div>
                </div>
            </div>
        </div>
    </footer>
      
    </div>
  )
}

